.scroll{
    position: fixed;
    right: 3rem;
    bottom: 5%;
    background-color: var(--tic);
    opacity: 0.8rem;
    padding: 0.5rem;
    border-radius: 10px;
    z-index: var(--zt);
    transition: 0.5s;
    color: #fff;
    font-size: 20px;
}
.s-icons{
    font-size: 1.5rem;
    color: var(--cc);
}

.show-scroll{
    bottom: 3rem;
}

@media screen and (max-width:992px){
    .scroll{
        right: 1.5rem;
    }
}