.s-container{
    grid-template-columns: repeat(2,350px);
    column-gap: 3rem;
    justify-content: center;
}

.s-content{
    background-color: var(--cc);
    border: 1px solid rgba(0,0,0,0.2);
    padding: 2rem 4rem;
    border-radius: 20px;
}

.s-title{
    font-size: 1.7rem;
    font-weight: var(--fm);
    text-align: center;
    margin-bottom: 1.5rem;
}

.s-content .s-box{
    display: flex;
    justify-content: center;
    column-gap: 3rem;
}

.s-group{
    display: grid;
    align-items: flex-start;
    row-gap: 1rem;
}

.s-data{
    display: flex;
    column-gap: 1rem;
}


.bx-badge-check{
    font-size: 1.2rem;
    color: var(--tic);
    line-height: 50px;
}

.s-level{
    font-size: 1rem;
}


@media screen and (max-width:768px){
    .s-container{
        grid-template-columns: 1fr;
        row-gap: 2rem;
    }

    .s-content{
        padding: 1.5rem;
    }
}

  