.footer{
    background-color: var(--cc);
    border-top: 1px olsid rgba(0,0,0,0.1);
}

.f-container{
    padding: 2rem 0rem 5rem;
}

.f-title,
.f-link{
    color: var(--tic);
}

.f-title{
    text-align: center;
    margin-bottom: 1rem;
}

.f-list{
    display: flex;
    justify-content: center;
    column-gap: 2rem;
    margin-bottom: 2rem;
}

.f-social{
    display: flex;
    justify-content: center;
    column-gap: 1.5rem;
}

.f-socialicon{
    padding: 0.1rem 0.5rem;
    border-radius:10px;
    display: inline-flex;
    color: var(--cc);
    background-color: var(--tic);
    font-size: 1.5rem;
}

.f-copy{
    display: block;
    margin-top: 3rem;
    text-align: center;
}