.c-container{
    grid-template-columns: repeat(2,max-content);
    justify-content: center;
    column-gap: 5rem;
    padding-bottom: 3rem;
}

.c-info{
    display: grid;
    row-gap: 1rem;
    grid-template-columns: 300px;
}

.c-form{
    width: 400px;
}

.c-title{
    text-align: center;
    font-size: 1.3rem;
    font-weight: var(--fn);
    margin-bottom: 2rem;
}

.c-card{
    background-color: var(--cc);
    border: 1px solid rgba(0,0,0,0.2);
    padding: 1rem;
    border-radius: 1rem;
    text-align: center;
}

.c-cardicon{
    font-size: 2rem;
    color: var(--tic);
    margin-bottom: 0.5rem;
}

.c-cardtitle,
.c-carddata{
    font-size: 1rem;
}

.c-carddata{
    display: block;
    margin-bottom: 0.5rem;
}

.c-btn{
    display: inline-flex;
    align-items: center;
    justify-content: center;
    column-gap: 0.5rem;
    color: var(--txc);
}

.c-formdiv{
    position: relative;
    margin-bottom: 2rem;
    height: 4rem;
}

.c-forminput{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 2px solid rgba(0,0,0,0.2);
    background: none;
    outline: none;
    border-radius: 1rem;
    z-index: 1;
    padding: 1.5rem;
}

.c-formtag{
    position: absolute;
    top: -1rem;
    left: 1.25rem;
    padding: 0.25rem;
    z-index: 10;
    font-size: 1rem;
    background-color: var(--bc);
}

.c-formarea{
    height: 12rem;
}

@media screen and (max-width:992px){
    .c-container{
        column-gap: 3rem;
    }
}

@media screen and (max-width:768px){
    .c-container{
        grid-template-columns: 1fr;
        row-gap: 3rem;
    }

    .c-info{
        justify-content: center;
    }

    .c-form{
        margin: 0 auto;
    }
}

@media screen and (max-width:375px){
    .c-form{
        width: 100%;
    }
}


@media screen and (max-width:320px){
    .c-form{
        width: 100%;
    }
}