.service-container{
    grid-template-columns: repeat(3,300px);
    justify-content: center;
    column-gap: 2rem;
}

.service-content{
    position: relative;
    border: 1px solid rgba(0,0,0,0.2);
    background-color: var(--cc);
}

.service-icon{
    display: block;
    font-size: 3rem;
    text-align: center;
    color: var(--tic);
    margin-bottom: 0.5rem;
}

.service-title{
    font-size: 1.3rem;
    font-weight: var(--fn);
    margin-bottom: 1rem;
    text-align: center;
}

.service-btn{
    color: var(--tic);
    font-size: 1rem;
    display: inline-flex;
    display: flex;
    justify-content: center;
    align-items: center;
    column-gap: 0.5rem;
    cursor: pointer;
}

.service-btnicon{
    transition: 0.5s;
    font-size: 1.5rem;
}

.service-btn:hover .service-btnicon{
    transform: translateX(0.5rem);
}

.service-modal{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,0.2);
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0rem 1rem;

    opacity: 0;
    visibility: hidden;

    transition: 0.5s;
    z-index: var(--zm);
}

.service-modal-content{
    width: 500px;
    position: relative;
    background-color: var(--cc);
    padding: 5rem 3rem 3rem;
    border-radius: 2rem;
}

.service-modalclose{
    position: absolute;
    top: 1.5rem;
    right: 1.5rem;
    font-size: 2rem;
    color: var(--tic);
    cursor: pointer;
}

.service-modal-title,
.service-modal-desc{
    text-align: center;
}

.service-modal-title{
    font-size: 1.5rem;
    margin-bottom: 1rem;
}

.service-modal-desc{
    font-size: 1.1rem;
    margin-bottom: 1rem;
}

.service-modalli{
    display: flex;
    align-items: center;
    column-gap: 1rem;
}

.service-modalicon{
    font-size: 1.1rem;
    color: var(--ticd);
}

.service-modalinfo{
    font-size: 1rem;
}

/* active modal */
.active-modal{
    opacity: 1;
    visibility: visible;
}


@media screen and (max-width:992px){
    .service-container{
        grid-template-columns: repeat(3,240px);
    }
}

@media screen and (max-width:768px){
    .service-container{
        grid-template-columns: repeat(3,200px);
    }

    .service-content{
        padding: 1rem;
    }
}

@media screen and (max-width:576px){
    .service-container{
        grid-template-columns: repeat(2,1fr);
    }
}

@media screen and (max-width:375px){
    .service-container{
        grid-template-columns: 1fr;
    }
}