.h-content{
    grid-template-columns:140px repeat(2,1fr);
    padding-top: 5.5rem;
    column-gap: 2rem;
    align-items: center;
}

.h-img{
    background: url(../../assets/3.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    width: 400px;
    height: 400px;
    box-shadow: inset 0 0 0 10px rgb(255 255 255 / 30%);
    order: 1;
    justify-self: center;
    animation:animate 7s ease-in-out infinite 1s;
}

@keyframes animate{
    0%{
        border-radius: 60% 40% 30% 70% / 60% 30% 70% 40%;
    }
    50%{
        border-radius: 30% 60% 70% 40% / 50% 60% 30% 60%;
    }
    100%{
        border-radius: 60% 40% 30% 70% / 60% 30% 70% 40%;
    }
}

.h-social{
    display: grid;
    grid-template-columns:max-content;
    row-gap: 1rem;
}

.h-socialicon{
    font-size: 1.5rem;
    color: var(--tic);
}

.h-socialicon:hover{
    color: var(--ticd);
}

.h-title{
    font-size: 3rem;
    margin-bottom: 0.5rem;
}

.h-hand{
    width: 35px;
    height: 35px;
    margin-left: 0.5rem;
}

.h-subtitle{
    position: relative;
    font-size: 2rem;
    padding-left: 5rem;
    font-weight: var(--fn);
    margin-bottom: 1rem;
}

.h-subtitle::before{
    content: "";
    position: absolute;
    width: 70px;
    height: 2px;
    background: var(--txc);
    left: 0%;
    top: 1.2rem;
}

.h-desc{
    max-width: 400px;
    margin-bottom: 3rem;
}

.h-container{
    row-gap: 7rem;
}

.h-scroll{
    margin-left: 10rem;
}

.wheel{
    animation:scroll 3s ease infinite;
}

@keyframes scroll{
    0%{
        transform: translateY(0);
    }
    30%{
        transform: translateY(3.75rem);
    }
}

.h-scrollname{
    color: var(--tic);
    font-weight: var(--fm);
    margin-right: 0.5rem;
    margin-left: 2px;
}

.h-scrollarrow{
    font-size: 1.5rem;
    color: var(--tic);
}


@media screen and (max-width:992px){
    .h-content{
        grid-template-columns:100px repeat(2,1fr);
        column-gap: 1.5rem;
    }

    .h-subtitle{
        padding-left: 5rem;
        margin-bottom: 1rem;
    }

    .h-subtitle::before{
        left: 0%;
        width: 60px;
    }

    .h-desc{
        max-width: initial;
        margin-bottom: 2rem;
    }
    
    .h-img{
        width: 270px;
        height: 270px;
    }

    .h-scroll{
        margin-left: 8rem;
    }

    .h-hand{
        width: 30px;
        height: 30px;
    }
}
   
@media screen and (max-width:768px){
    .h-content{
        grid-template-columns: 0.5fr 3fr;
        padding-top: 3rem;
    }

    .h-img{
        order: initial;
        justify-self: initial;
        box-shadow: inset 0 0 0 5px rgb(255 255 255 / 30%);
        height: 240px;
        width: 240px;
    }

    .h-data{
        grid-column: 1/3;
    }

    .h-scroll{
        display: none;
    }
}

@media screen and (max-width:425px){
    .h-title{
        font-size: 2.7rem;
    }

    .h-subtitle{
        font-size: 1.6rem;
    }
}

@media screen and (max-width:350px){
    .h-content{
        grid-template-columns: 0.5fr 3fr;
        padding-top: 0.1rem;
    }

    .h-img{
        width: 200px;
        height: 200px;
    }

    .h-subtitle{
        font-size: 1.5rem;
    }

    .h-desc{
        font-size: 1.1rem;
    }
}