.q-container{
    max-width: 768px;
}

.q-tabs{
    display: flex;
    justify-content: center;
    margin-bottom: 2rem;
}

.q-btn{
    font-size: 1.1rem;
    font-weight: var(--fn);
    color: var(--tic);
    margin-right: 1rem;
    cursor: pointer;
}

.q-btn:hover{
    color: var(--ticd);
}

.q-icon{
    font-size: 1.5rem;
    margin-right: 0.25rem;
}

.q-sections{
    display: grid;
    grid-template-columns:1fr;
    justify-content: center;
}

.q-content{
    display: none;
}

.q-contentactive{
    color: var(--ticd);
    display: block;
}

.q-data{
    display: grid;
    grid-template-columns:1fr max-content 1fr;
    column-gap: 2rem;
    margin-bottom: 2rem;
}

.q-title{
    font-size: 1.1rem;
}

.q-subtitle{
    display: inline-block;
}

.q-rounder{
    display: inline-block;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-color: #000;
}

.q-line{
    display: block;
    width: 2px;
    height: 140%;
    background: rgb(0, 0, 0);
    transform: translate(6px,-7px);
}

@media screen and (max-width:992px){
    .q-container{
        margin-left: auto;
        margin-right: auto;
    }
}

@media screen and (max-width:768px){
    .q-container{
        margin-left: 2rem;
        margin-right:2rem;
    }
}   

@media screen and (max-width:576px){
    .q-sections{
        grid-template-columns: initial;
    }
}   

@media screen and (max-width:425px){
    .q-sections{
        grid-template-columns: initial;
    }
}  

@media screen and (max-width:320px){
    .q-data{
        grid-template-columns:1fr max-content;
        column-gap: 2rem;
    }
}    