.a-container{
    grid-template-columns: repeat(2,1fr);
    align-items: center;
    column-gap: 5rem;
}

.a-img{
    width: 350px;
    border-radius: 2rem;
    justify-self: center;
}
.a-info{
    grid-template-columns: repeat(3,150px);
    gap: 1rem;
    margin-bottom: 2rem;
}

.a-box{
    background-color: var(--cc);
    border: 1px solid rgba(0,0,0,0.2);
    border-radius: 1rem;
    text-align: center;
    padding: 1rem 1.5rem;
}

.a-box i{
    font-size:1.6rem;
    color: var(--tic);
    margin-bottom: 0.5rem;
}

.a-title{
    font-size: 1.1rem;
    font-weight: 500;
}

.a-desc{
    padding: 0rem 5rem 0rem 0rem;
    margin-bottom: 2rem;
    font-size: 0.9rem;
}

@media screen and (max-width:992px){
    .a-container{
        grid-template-columns: 1fr;
        row-gap: 2rem;
    }

    .a-img{
        width: 300px;
    }

    .a-box{
        padding: 0.9rem 0.5rem;
    }

    .a-data{
        text-align: center;
    }

    .a-info{
        justify-content: center;
    }

    .a-desc{
        padding: 0rem 5rem;
        margin-bottom: 2rem;
        font-size: 1rem;
    }
}

@media screen and (max-width:768px){
    .a-info{
        grid-template-columns: repeat(3,1fr);
    }

    .a-desc{
        padding: 0;
    }
}

@media screen and (max-width:1440px){
    .section-title{
        padding-top: 3rem;
    }
}

@media screen and (max-width:450px){
    .a-desc{
        padding:0 1rem;
    }
}

@media screen and (max-width:375px){
    .a-info{
        grid-template-columns: repeat(2,1fr);
    }
}

@media screen and (max-width:350px){
    .a-info{
        grid-template-columns: repeat(2,1fr);
    }
}