@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&family=Rubik:wght@400;500;600;700&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html{
  scroll-behavior: smooth;
}

body,
button,
input,
textarea{
  font-family: var(--bf);
  font-size: var(--nfs);
}

body{
  background-color: var(--bc);
  color: var(--txc);
}

h1,h2,h3{
  color: var(--tic);
  font-weight: var(--fsb);
}

ul{
  list-style: none;
}

a{
  text-decoration: none;
}

button{
  cursor: pointer;
  border: none;
  outline: none;
}

img{
  max-width: 100%;
  height: auto;
}

.section{
  padding: 6rem 0rem 2rem;
}

.section-title{
  font-size: 2rem;
  color: var(--tic);
}

.section-subtitle{
  font-size: 1.5rem;
  text-align: center;
  display: block;
  margin-bottom: 4rem;
}

.section-title,
s.section-subtitle{
  text-align: center;
}

.container{
  max-width: 970px;
  margin-left: auto;
  margin-right: auto;
}

.grid{
  display: grid;
  gap: 1.5rem;
}

.btn{
  display: inline-block;
  background: var(--tic);
  color: var(--cc);
  padding: 0.8rem 1.5rem;
  border-radius: 1rem;
  font-weight: var(--fn);
  font-size: 1.1rem;
}

.btn:hover{
  background-color: var(--ticd);
}

.btn-icon{
  margin-left: 0.5rem;
  color: #fff !important;
}

.btn-flex{
  display: inline-flex;
  align-items: center;
}

:root{
  --hh:3rem;

  --hue:0;
  --sat:0%;
  --tic:hsl(0,0%,20%);
  --ticd:hsl(0,0%,0%);
  --txc:hsl(0,0%,46%);
  --bc:hsl(0,0%,98%);
  --cc:#ffff;

  --bf:"Poppins",sans-serif;

  --fn:500;
  --fm:600;
  --fsb:600;

  --mb25:0.25rem;
  --mb05:0.5rem;
  --mb1:1rem;
  --mb75:0.75rem;
  --mb15:1.5rem;
  --mb2:2rem;
  --mb255:2.5rem;
  --mb3:3rem;

  --zt:10;
  --zf:100;
  --zm:1000;
}

@media screen and (max-width:992px){
  :root{
    --bfs:2.8rem;
    --h1:1.5rem;
    --h2:1.25rem;
    --h3:1rem;
    --nfs:0.9rem;
    --sfz:0.8rem;
    --ssfs:0.75rem;
  }
}

@media screen and (max-width:992px){
 .container{
  margin-left: var(--mb15);
  margin-right:var(--mb15) ;
 }

}

@media screen and (max-width:768px){
  body{
    margin: 0 0 3rem 0;
  }

  section{
    padding: 2rem 0rem 4rem;
  }

  .section-subtitle{
    margin-bottom: 3rem;
  }
}

