.w-container{
    grid-template-columns: repeat(3,max-content);
    gap: 3rem;
    justify-content: center;
}

.w-img{
    width: 300px;
    border-radius: 1rem;
    margin-bottom: 1rem;
}

.w-card{
    background-color: var(--cc);
    padding: 1.5rem;
    border-radius: 1rem;
}

.w-title{
    font-size: 1.2rem;
}

.w-btn{
    display: flex;
    align-items: center;
    column-gap: 0.25rem;
    color: var(--tic);
}
.w-btnicon{
    font-size: 1.2rem;
    transition: 0.5s;
}

.w-card:hover .w-btnicon{
    transform: translateX(0.3rem);
}

.w-filters{
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: 1rem;
    margin-bottom: 2rem;
    text-transform: capitalize;
}

.w-item{
    padding: 0.25rem 1rem;
    border-radius: 1rem;
    text-transform: capitalize;
    cursor: pointer;
    color: var(--tic);
    transition: 0.5s ease;
}

.w-item:hover{
    background-color: var(--tic);
    color: #fff;
}

.active-work{
    padding: 0.25rem 1rem;
    border-radius: 1rem;
    text-transform: capitalize;
    cursor: pointer;
    color: var(--tic);
    transition: 0.5s ease;
}

.active-work:hover{
    background-color: var(--tic);
    color: #fff;
}

@media screen and (max-width:992px){
.w-container{
    gap: 1.25rem;
    grid-template-columns: repeat(2,max-content);
}
.w-card{
    padding: 1rem;
}

.w-item{
    font-size: 1.1rem;
}

.work-active{
    font-size: 1.1rem;
}
}

@media screen and (max-width:560px){
    .w-container{
        grid-template-columns: repeat(1,max-content);
        row-gap: 0.5rem;
    }
}

@media screen and (max-width:425px){
    .w-container{
        grid-template-columns: repeat(1,max-content);
        row-gap: 0.5rem;
    }
}

@media screen and (max-width:320px){
    .w-img{
        width: 250px;
    }
}